/**
 * 메타 컴포넌트
 *
 * @author RWB
 * @since 2022.02.26 Sat 10:33:54
 */

import { Helmet } from 'react-helmet-async';
import { SITE_NAME } from '../../global/env';

interface Props {
	title: string
}

/**
 * 메타 Element 반환 메서드
 *
 * @param {Props} param0: 프로퍼티
 *
 * @returns {JSX.Element} Element
 */
export default function Meta({ title }: Props): JSX.Element
{
	const fullTitle = `${title} - ${SITE_NAME}`;

	return (
		<Helmet>
			<meta name="description" content={SITE_NAME} />

			<meta property="og:site_name" content={SITE_NAME} />
			<meta property="og:title" content={fullTitle} />
			<meta property="og:description" content={SITE_NAME} />
			<meta property="og:type" content="website" />
			<meta property="og:url" content="https://project.itcode.dev/kakaostyle" />
			<meta property="og:locale" content="ko_KR" />
			<meta property="og:image" content="https://user-images.githubusercontent.com/50317129/155823631-e3d8fe32-f631-427c-80d4-691c6e7ceeac.png" />

			<title>{fullTitle}</title>

			<script async src="https://www.googletagmanager.com/gtag/js?id=G-1YPNLPR0CQ"></script>
			<script src="https://project.itcode.dev/js/ga.js"></script>
		</Helmet>
	);
}