import { API_BASE } from '../global/env';
import { ResponseProps } from '../global/util';

export const URL = `${API_BASE}/vacation`;

export interface VacationProps
{
	idx: number,
	id: string,
	begin: Date,
	end: Date,
	count: number,
	comment?: string,
	reg_date: Date
}

export interface VacationPostProps
{
	begin: Date,
	end: Date,
	comment?: string,
	type: number
}

/**
 * 연차 정보 리스트 호출 API 응답 반환 메서드
 *
 * @param {string} auth: JWT 토큰
 *
 * @returns {Promise} API 응답
 */
export async function getVacations(auth: string): Promise<VacationProps[]>
{
	let answer: VacationProps[];

	// API 호출 시도
	try
	{
		const response = await fetch(`${URL}`, {
			method: 'GET',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': auth
			}
		});

		const json = await response.json();

		// 정상 응답일 경우
		if (response.ok)
		{
			answer = json.body;
		}

		// 아닐 경우
		else
		{
			answer = [];
		}
	}

	// 예외
	catch (e)
	{
		answer = [];
	}

	return answer;
}

/**
 * 연차 정보 추가 API 응답 반환 메서드
 *
 * @param {string} auth: JWT 토큰
 * @param {VacationPostProps} vacation: 연차 추가 객체
 *
 * @returns {Promise} API 응답
 */
export async function postVacation(auth: string, vacation: VacationPostProps): Promise<ResponseProps>
{
	let answer: ResponseProps;

	// API 호출 시도
	try
	{
		const response = await fetch(`${URL}`, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': auth
			},
			body: JSON.stringify(vacation)
		});

		// 정상 응답일 경우
		if (response.ok)
		{
			answer = {
				isSuccess: true,
				body: ''
			};
		}

		// 아닐 경우
		else
		{
			const json = await response.json();

			answer = {
				isSuccess: false,
				body: json.message
			};
		}
	}

	// 예외
	catch (e: unknown)
	{
		const err = e as Error;

		answer = {
			isSuccess: false,
			body: err.message
		};
	}

	return answer;
}

/**
 * 연차 정보 삭제 API 응답 반환 메서드
 *
 * @param {string} auth: JWT 토큰
 * @param {number} idx: 인덱스
 *
 * @returns {Promise} API 응답
 */
export async function deleteVacation(auth: string, idx: number): Promise<boolean>
{
	let answer: boolean;

	// API 호출 시도
	try
	{
		const response = await fetch(`${URL}/${idx}`, {
			method: 'DELETE',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': auth
			}
		});

		answer = response.ok;
	}

	// 예외
	catch (e)
	{
		answer = false;
	}

	return answer;
}