import { atom } from 'recoil';
import { AccountProps } from '../api/accountApi';
import { VacationProps } from '../api/vacationApi';

export const loginAtom = atom({
	key: 'loginState',
	default: ''
});

export const vacationListAtom = atom({
	key: 'vacationListState',
	default: [] as VacationProps[]
});

export const accountAtom = atom({
	key: 'accountState',
	default: undefined as AccountProps | undefined
});

export const renderAtom = atom({
	key: 'renderState',
	default: 0
});