import { ReactElement, useEffect } from 'react';
import { useRecoilState, useRecoilValue } from 'recoil';
import { getAccount } from '../../api/accountApi';
import { accountAtom, loginAtom, renderAtom } from '../../global/state';
import './AccountCard.scss';

/**
 * 계정 카드 ReactElement 반환 메서드
 *
 * @returns {ReactElement} ReactElement
 */
export default function AccountCard(): ReactElement
{
	const loginState = useRecoilValue(loginAtom);
	const renderState = useRecoilValue(renderAtom);
	const [ accountState, setAccountState ] = useRecoilState(accountAtom);

	useEffect(() =>
	{
		(async () =>
		{
			const response = await getAccount(loginState);

			response && setAccountState(response);
		})();
	}, [ renderState ]);

	return (
		<article id='account-card'>
			<div>
				<div>
					<img src='https://devblog.croquis.com/img/kakaostyle-icon.png' width={64} />
				</div>

				<div>
					<p>{accountState ? accountState.dept : '부서명'} {accountState ? accountState.name : '이름'}</p>
					<p>남은 연차: <span>{accountState ? accountState.count : 0}일</span></p>
				</div>
			</div>
		</article>
	);
}