import { ReactElement } from 'react';
import Header from '../components/header/Header';
import AccountCard from '../components/home/AccountCard';
import VacationList from '../components/home/VacationList';
import Footer from '../components/footer/Footer';
import './Home.scss';
import Meta from '../components/global/Meta';

/**
 * 홈 페이지 ReactElement 반환 메서드
 *
 * @returns {ReactElement} ReactElement
 */
export default function Home(): ReactElement
{
	return (
		<section id='home'>
			<Meta title='🏠 Home' />

			<Header />

			<article id='banner'>
				<div>
					<h1>'나'로 가득한 라이프 스타일</h1>
					<h1>카카오스타일</h1>
				</div>

				<div>
					<h3>내가 좋아하는 무언가를 발견했을 때의 즐거움</h3>
					<h3>그 즐거움을 찾으러 떠나보세요!</h3>
				</div>
			</article>

			<AccountCard />
			<VacationList />

			<Footer />
		</section>
	);
}