/**
 * 연차 리스트 컴포넌트
 *
 * @author RWB
 * @since 2021.12.22 Tue 01:09:16
 */

import { BaseSyntheticEvent, ReactElement, useEffect, useState } from 'react';
import DatePicker from 'react-datepicker';
import { useRecoilState, useRecoilValue } from 'recoil';
import { deleteVacation, getVacations, postVacation, VacationPostProps, VacationProps } from '../../api/vacationApi';
import { loginAtom, renderAtom, vacationListAtom } from '../../global/state';
import { getDateDetail } from '../../global/util';

import 'react-datepicker/dist/react-datepicker.css';
import './VacationList.scss';
import Icon from '@mdi/react';
import { mdiAirplane, mdiTrashCan } from '@mdi/js';

/**
 * 연차 리스트 ReactElement 반환 메서드
 *
 * @returns {ReactElement} ReactElement
 */
export default function VacationList(): ReactElement
{
	const renderState = useRecoilValue(renderAtom);
	const loginState = useRecoilValue(loginAtom);
	const [ vacationListState, setVacationListState ] = useRecoilState(vacationListAtom);

	useEffect(() =>
	{
		(async () =>
		{
			const response = await getVacations(loginState);

			setVacationListState(response);
		})();
	}, [ renderState ]);

	return (
		<article id='vacation-list'>
			<div data-tab='meta'>
				<h3>연차 신청 현황</h3>

				<button onClick={() =>
				{
					const tag = document.querySelector('[data-tab=form]') as HTMLDivElement;

					tag.setAttribute('data-status', 'true');
				}}>+ 추가</button>
			</div>

			<VacationForm />

			<div data-tab='items'>
				{vacationListState.map((e, i) => <VacationItem key={i} {...e} />)}
			</div>
		</article>
	);
}

/**
 * 연차 리스트 아이템 ReactElement 반환 메서드
 *
 * @param {VacationProps} param: VacationProps 객체
 *
 * @returns {ReactElement} ReactElement
 */
function VacationItem({ idx, id, begin, end, count, comment, reg_date }: VacationProps): ReactElement
{
	const [ renderState, setRenderState ] = useRecoilState(renderAtom);
	const loginState = useRecoilValue(loginAtom);
	const beginDetail = getDateDetail(begin);
	const endDetail = getDateDetail(end);
	const regDetail = getDateDetail(reg_date);

	return (
		<div className='vacation'>
			<div className='info'>
				<div className='meta'>
					<h3>VACATION TICKET</h3>
					<h4><small>No. </small>{idx}</h4>

					<div>
						<p>USER: {id}</p>
						<p>REGDATE: {`${regDetail.year}.${regDetail.month}.${regDetail.day} ${regDetail.hour}:${regDetail.minute}:${regDetail.second}`}</p>
					</div>
				</div>

				<div className='detail'>
					<div className='days'>
						<div>
							<p className='date'>{`${beginDetail.year}.${beginDetail.month}.${beginDetail.day}`}</p>
							<p className='place'>KAKAO</p>
						</div>

						<div>
							<Icon path={mdiAirplane} size={2} color="dodgerblue" rotate={45} />
							<p className='day-text'>{count}days</p>
						</div>

						<div>
							<p className='date'>{`${endDetail.year}.${endDetail.month}.${endDetail.day}`}</p>
							<p className='place'>ANYWHERE</p>
						</div>
					</div>

					<div className='option'>
						<p>COMMENT: {comment || '없음'}</p>
					</div>
				</div>
			</div>

			<div className='action'>
				<button onClick={async () =>
				{
					const response = await deleteVacation(loginState, idx);

					// 응답이 정상적일 경우
					if (response)
					{
						setRenderState(renderState + 1);
					}
				}}>
					<Icon path={mdiTrashCan} size={1.5} />
				</button>
			</div>
		</div>
	);
}

/**
 * 연차 추가 폼 ReactElement 반환 메서드
 *
 * @returns {ReactElement} ReactElement
 */
function VacationForm(): ReactElement
{
	const loginState = useRecoilValue(loginAtom);
	const [ renderState, setRenderState ] = useRecoilState(renderAtom);
	const [ startState, setStartState ] = useState(new Date('2022-01-01'));
	const [ endState, setEndState ] = useState(null as Date | null);
	const [ radioState, setRadioState ] = useState(false);

	return (
		<div data-tab='form' data-status='false'>
			<form onSubmit={async (e: BaseSyntheticEvent) =>
			{
				e.preventDefault();

				const type: HTMLInputElement = document.querySelector('input[name=type]:checked') as HTMLInputElement;

				const vacation: VacationPostProps = {
					begin: startState,
					end: endState as Date,
					comment: e.target.comment.value,
					type: parseFloat(type.value)
				};

				const response = await postVacation(loginState, vacation);

				const message: HTMLElement = document.querySelector('p.message') as HTMLElement;

				// 응답이 정상적일 경우
				if (response.isSuccess)
				{
					setRenderState(renderState + 1);

					message.innerHTML = '';

					const tag = document.querySelector('[data-tab=form]') as HTMLDivElement;
					tag.setAttribute('data-status', 'false');
				}

				// 아닐 경우
				else
				{
					message.innerHTML = response.body;
				}
			}}>
				<div>
					<DatePicker
						name='date'
						selected={startState}
						startDate={startState}
						endDate={endState}
						dateFormat='Y-MM-dd'
						selectsRange
						required
						minDate={new Date('2022-01-01')}
						maxDate={new Date('2022-12-31')}
						onCalendarClose={() =>
						{
							// 마지막 날짜 선택이 안 됐을 경우
							if (endState === null)
							{
								setEndState(startState);
								setRadioState(false);
							}

							// 시작, 마지막 날짜가 전부 선택됐을 경우
							else
							{
								setRadioState(startState.getTime() !== endState.getTime());

								const tag: HTMLInputElement | null = document.querySelector('[type=radio]');

								// 라디오버튼이 유효한 값을 가질 경우
								if (tag)
								{
									tag.checked = true;
								}
							}
						}}
						onChange={(date) =>
						{
							// 배열일 경우
							if (Array.isArray(date))
							{
								date[0] && setStartState(date[0]);
								setEndState(date[1]);
							}
						}} />
				</div>

				<div className='group'>
					<div>
						<input id='r1' type='radio' name='type' value='1' required />
						<label htmlFor='r1'>연차</label>
					</div>

					<div>
						<input id='r2' type='radio' name='type' value='0.5' disabled={radioState} required />
						<label htmlFor='r2'>반차</label>
					</div>

					<div>
						<input id='r3' type='radio' name='type' value='0.25' disabled={radioState} required />
						<label htmlFor='r3'>반반차</label>
					</div>
				</div>

				<div>
					<input name='comment' type='text' maxLength={100} />
				</div>

				<div>
					<p className='message'></p>
				</div>

				<div className='buttons'>
					<input type='submit' value='신청' />
					<input type='reset' value='취소' onClick={() =>
					{
						const tag = document.querySelector('[data-tab=form]') as HTMLDivElement;
						tag.setAttribute('data-status', 'false');

						const message: HTMLElement = document.querySelector('p.message') as HTMLElement;
						message.innerHTML = '';

						setStartState(new Date('2022-01-01'));
						setEndState(null);
					}} />
				</div>
			</form>
		</div>
	);
}